.IntroWrapper {
    display: flex;
    padding: 25px 100px;
    gap: 100px;
}

.IntroImageContainer {
    display: flex;
    width: 50%;
}

.IntroImage {
    width: 100%;
    object-fit: cover;
}

.IntroMessage {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 25px;
    width: 50%;
}

.IntroMessageList{
    padding: 0;
    display: flex;
    flex-direction: column;
    width: 100%;
}

.IntroMessageListItem {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    gap: 10px;
}

.IntroMessageListItemTitle{
    border-top: 10px solid var(--black);
    border-bottom: 10px solid var(--black);
    padding: 10px 15px;
    border-radius: 10px;
    background-color: var(--green);
    margin: 0px;
    margin-right: auto;
}

.IntroMessageListItemText {
    font-weight: 300;
    line-height: 1.5;
}

.IntroFeatureWrapper {
    display: flex;
    align-items: center;
    gap: 15px;
}

.IntroFeatureIcon {
    color: var(--green);
    padding: 7px;
    border-radius: 50%;
    background-color: var(--black);
    box-shadow: 0px 5px 10px var(--lightBlack);
}

.IntroFeatureText {
    padding: 10px;
    background-color: var(--lightGreen);
    border: 3px solid var(--green);
    border-radius: 10px;
    box-shadow: 0px 3px 5px var(--lightBlack);
}

.IntroCallToAction {
    border: 3px solid var(--green);
    display: flex;
    font-size: 1rem;
    font-weight: 700;
    padding: 10px;
    color: var(--black);
    border-radius: 5px;
    cursor: pointer;
    box-shadow: 0px 3px 5px var(--lightBlack);
    transition: 0.3s;
}

.IntroCallToAction:hover {
    border: 3px solid var(--black);
    background-color: var(--green);
    color: var(--black);
    box-shadow: 0px 3px 5px var(--lightBlack);
    scale: 1.1;
}