.DashboardNewJobApplicationForm {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    padding: 25px;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    gap: 25px;
}

.DashboardNewJobApplicationFormWrapper {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.DashboardNewJobApplicationFormSet {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 5px;
}

.DashboardNewJobApplicationFormSetError {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 5px;
}

.DashboardNewJobApplicationFormSetError input{
    display: flex;
    margin-right: auto;
    font-size: 1rem;
    font-weight: 500;
    padding: 5px;
    width: 93%;
    border: 2px solid var(--red);
    border-radius: 10px;
    background-color: var(--lightRed);
    transition: 0.3s;
  }

  .DashboardNewJobApplicationFormSetError input:hover {
    background-color: var(--lightRed);
    border: 2px solid var(--red);
  }

  .DashboardNewJobApplicationFormSetError input:focus {
    background-color: var(--lightRed);
    border: 2px solid var(--red);
  }

  .DashboardNewJobApplicationFormSetError input:active {
    background-color: var(--lightRed);
    border: 2px solid var(--red);
  }

.DashboardNewJobApplicationFormSetSuggestion{
    padding: 5px 10px;
    border-radius: 5px;
    background-color: var(--lightGray);
    border: 2px solid var(--lightGray);
    color: var(--gray);
    font-size: 0.75rem;
    margin-right: auto;
    text-align: left;

}

.DashboardNewJobApplicationFormSetSuggestionError {
    padding: 5px 10px;
    border-radius: 5px;
    background-color: var(--lightRed);
    border: 2px solid var(--red);
    color: var(--red);
    font-size: 0.75rem;
    margin-right: auto;

}

.DashboardNewJobApplicationFormSetTitle{
    color: var(--black);
    font-weight: 500;
}

.DashboardNewJobApplicationFormSet input{
    display: flex;
    margin-right: auto;
    font-size: 1rem;
    font-weight: 500;
    padding: 5px;
    width: 93%;
    border: 2px solid var(--gray);
    border-radius: 10px;
    background-color: white;
    transition: 0.3s;
    font-size: 1rem;
  }

  .DashboardNewJobApplicationFormSet input:hover {
    background-color: var(--lightGreen);
    border: 2px solid var(--green);
  }

  .DashboardNewJobApplicationFormSet input:focus {
    background-color: var(--lightGreen);
    border: 2px solid var(--green);  }

  .DashboardNewJobApplicationFormSet input:active {
    background-color: var(--lightGreen);
    border: 2px solid var(--green);
  }

.DashboardNewJobApplicationFormSetButton {
    width: 100%;
    justify-content: space-between;
    display: flex;
    flex-direction: row;
    gap: 5px;
}

.DashboardNewJobApplicationFormFavoriteButton {
    display: flex;
    align-items: center;
    gap: 5px;
    font-size: 1rem;
    border: 2px solid var(--gray);
    background-color: transparent;
    color: var(--gray);
    display: flex;
    align-items: center;
    padding: 5px 10px;
    border-radius: 5px;
    cursor: pointer;
    transition: 0.3s;
}

.DashboardNewJobApplicationFormFavoriteButton:hover {
    border: 2px solid var(--gold);
    background-color: #e09f3e27;
    color: var(--gold);
}

.DashboardNewJobApplicationFormFavoriteButton:active {
    border: 2px solid var(--gold);
    background-color: var(--gold);
    color: var(--white);
}

.DashboardNewJobApplicationFormFavoriteButtonFocus {
    display: flex;
    align-items: center;
    gap: 5px;
    font-size: 1rem;
    display: flex;
    align-items: center;
    padding: 5px 10px;
    border-radius: 5px;
    cursor: pointer;
    transition: 0.3s;
    border: 2px solid var(--gold);
    background-color: var(--gold);
    color: var(--white);
}

.DashboardNewJobApplicationFormSubmitButton {
    color: var(--black);
    font-size: 1.2rem;
    font-weight: 800;
    border: 2px solid var(--green);
    background-color: transparent;
    display: flex;
    align-items: center;
    padding: 5px 10px;
    border-radius: 5px;
    cursor: pointer;
    transition: 0.3s;
}

.DashboardNewJobApplicationFormSubmitButton:hover {
    border: 2px solid var(--black);
    background-color: var(--green);
}

.DashboardCard {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    width: 350px;
}

.DashboardCardContainer {
    display: flex;
    flex-direction: column;
    background-color: white;
    border-radius: 0px 0px 15px 15px;
    box-shadow: 0px 0px 15px var(--lightBlack);
}

.DashboardCardTopBar{
    display: flex;
    align-items: center;
    gap: 10px;
    padding: 0px 25px;
    background-color: var(--black);
    color: var(--green);
    border-radius: 15px 15px 0px 0px;
}

.DashboardCardTopBarTitle {
    font-size: 1rem;
}

.DashboardCardTopBarName {
    padding: 5px;
    border: 2px solid var(--green);
    background-color: #96e6b32f;
    border-radius: 5px;
    font-weight: 500;
}

.DashboardCardAddNew {
    display: flex;
    justify-content: center;
    align-items: center;
}
