.SignUpWrapper {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #191923b4;
  z-index: 5001;
  display: flex;
  align-items: center;
  justify-content: center;
}

.SignUpContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: white;
  gap: 25px;
  width: 35%;
  border-radius: 15px;
  box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.088);
}

.SignUpTopBar {
  padding: 10px 25px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  background-color: var(--black);
  border-radius: 15px 15px 0px 0px;
}

.SignUpTopBar h1 {
  font-size: 1rem;
  color: var(--green);
}

.SignUpCloseButton {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 2px solid var(--red);
  border-radius: 5px;
  background-color: #d5294061;
  color: var(--white);
  text-transform: uppercase;
  font-weight: 700;
  cursor: pointer;
  transition: 0.3s;
}

.SignUpCloseButton:hover {
  background-color: var(--red);
}

.SignUpList {
  display: flex;
  flex-direction: column;
  padding: 0px 0px 50px 0px;
}

.SignUpLogoWrapper {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
}

.SignUpLogo {
  width: 250px;
}

.SignUpForm {
  width: 350px;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.SignUpFieldSet {
  display: flex;
  flex-direction: column;
  gap: 15px;
  align-items: center;
  border: none;
}

.SignUpForm label {
  display: flex;
  margin-right: auto;
  font-size: 1rem;
  font-weight: 500;
}

.SignUpForm input {
  display: flex;
  margin-right: auto;
  font-size: 1rem;
  font-weight: 500;
  padding: 10px;
  width: 93%;
  border: 2px solid var(--gray);
  border-radius: 10px;
  background-color: white;
  transition: 0.3s;
}

.SignUpForm input:hover {
  box-shadow: 0px 0px 10px var(--green);
}

.SignUpSubmitButton {
  margin-top: 25px;
  display: flex;
  justify-content: center;
  font-size: 1rem;
  font-weight: 500;
  padding: 10px;
  border: 5px solid var(--green);
  border-radius: 10px;
  background-color: #f8fffb;
  transition: 0.3s;
  cursor: pointer;
}

.SignUpSubmitButton:hover {
  background-color: var(--green);
  border: 5px solid var(--black);
  box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.088);
}
